.container{
  width:100%;
  height: 100%;
  position: relative;
  .topContainer{
    background-color:#E6EAF0;
    .top{
      height:120px;
      width:1180px;
      margin:0 auto;
      position: relative;
      :global(.ant-breadcrumb){
        text-align: left;
        width:200px;
        position: absolute;
        top:34px;
        font-size: 16px;
        :global(.anticon-shopping){
          margin-right: 8px;
        }
      }
      .right{
        position: absolute;
        right: 0;
        text-align: right;
        width:50%;
        line-height:108px;
        .add{
          width:102px;
          height:40px;
          margin-right:3.9%;
          background-color:#6597FF;
          font-size: 14px;
          font-family: Roboto;
        }
        .search{
          width:251px;
          height:36px;
          color:#888888;
          :global(.ant-input-suffix){
            font-size:20px;
          }
        }
      }
    }
  }


  .table {
    background-color: #FFFFFF;
    width: 1180px;
    margin: 24px auto;
    :global(.ant-table-thead){
      background-color:#F0F2F5;
    }
    :global(.ant-table-thead tr th){
      background-color:#F0F2F5;
      min-width: 113px;
    }
    :global(.price .ant-table-column-sorter){
      left:83px;
    }
    :global(.registertime .ant-table-column-sorter){
      left:89px;
    }
    :global(.ant-table-column-sorter){
      color: #101010  !important;
      left:73px;
    }
    :global(.ant-table-tbody tr:nth-child(even)){
      background-color:#F0F2F5;
    }

    td, div {
      color: rgba(16, 16, 16, 1);
      font-family: SourceHanSansSC-regular;
      font-size: 14px;
    }
    a {
      color: rgba(101, 151, 255, 1);
      text-decoration: none;
    }
  }


  :global(.ant-pagination-total-text) {
    margin-top: -18px;
    flex: 1;
    margin-right:0!important;
    color: rgba(16, 16, 16, 1);
    font-size: 14px;
    text-align: left;
  }

  :global(.ant-pagination) {
    width:1180px;
    margin: 27px auto;
    display:flex;
  }
  :global(.ant-tabs-nav-wrap){
    text-align: left;
    background-color: #E6EAF0;

  }
  :global(.ant-tabs) {
    width: 1196px;
    margin: -40px auto 0 auto;
  }
  :global(.ant-tabs-card) {
    border: none;

    :global( .ant-tabs-content) {
      margin-top: -16px;

      :global(.ant-tabs-tabpane) {
        background: #fff;
        padding-top: 16px;
      }
      :global(.ant-table-placeholder){
        text-align: center!important;
      }
    }

    :global(.ant-tabs-bar) {
      border-color: #fff;
      background-color: #E6EAF0;

      :global(.ant-tabs-tab) {
        width: 100px;
        height: 40px;
        text-align: center;
        border: none;
        background: #F0F2F5;
        margin-right: 5px;
      }

      :global(.ant-tabs-tab-active) {
        border: none;
        background: #fff;
      }
    }
  }


}