:global(.ant-transfer-list-content-item){
    overflow: unset!important;
    padding-right:0;
  }
:global(.ant-input-number) {
  //margin-left: 10px;
  width:40px;
}
:global(.ant-input-number-handler-wrap){
  display:none
}
:global(.ant-transfer-list-header){
  background-color: #F0F2F5;
}
:global(.LazyLoad){
  height:40px!important;
  padding:2px 0;
  color: #333333;
}
:global(.LazyLoad:nth-child(even)){
  background-color: rgba(240, 242, 245, 0.5);
}
:global(.ant-transfer-operation){
 padding:0 18px;
}
:global(.ant-btn-icon-only){
  border-radius: 100%;
  width: 34px;
  height: 34px;
  //background-color: #CCCCCC!important;
  line-height: 34px;
}
:global(.ant-btn-primary[disabled]){
  background-color: #CCCCCC!important;
}
:global(.ant-transfer-operation .ant-btn .anticon){
  font-size: 17px;
  color: #FFFFFF;
}

